<template>
  <div id="home">
    <div class="container">
      <div class="header" v-show="!show">
        <img src="@/assets/logo.png" class="logo" alt="">
        <!-- <span @click="show=true">=</span> -->
        <img src="@/assets/more.png" class="more" @click="show=true" alt="">
      </div>
      <div class="content">
        <bannercom></bannercom>
        <router-view></router-view>
      </div>
    </div>
    <van-popup
      v-model="show"
      position="right"
      :style="{ height: '100%',width:'65%' }"
    >
      <sidecom @closeSide='closeSideF'></sidecom>
    </van-popup>
  </div>
</template>

<script>
import bannercom from '../components/banner'
import childcom from '../components/child'
import sidecom from '../components/side'
export default {
  data() {
    return {
      show:false
    };
  },
  components:{
    bannercom,
    childcom,
    sidecom
  },
  mounted(){
    if(location.href.indexOf('home')==-1){
      location.href = location.href+'home'
    }
  },
  methods:{
    closeSideF(val){
      this.show = false
    }
  }
};
</script>

<style lang="less">
#home{
  .container{
    .header{
      position: fixed;
      top: 0;
      left: 0;
      height: 60px;
      width: 100%;
      z-index: 9999;
      background: #fff;
      img.logo{
        height: 40px;
        padding: 10px 0 0 5px;
      }
      .more{
        float: right;
        // margin-top: 5px;
        width: 27px;
        height: 27px;;
        padding: 15px 10px 10px;
        margin-right: 1px;
      }
    }
    .content{
      margin-top: 60px;
    }
  }
}
</style>


