
<template>
  <div id="newshy">
    <titlecom :title="title"></titlecom>
    <div class="content-child">
        <img src="@/assets/newshy.jpg" alt="">
    </div>
  </div>
</template>

<script>
import titlecom from "../../components/title";
export default {
  data() {
    return {
      title: "行业动态"
    };
  },
  components: {
    titlecom
  },
  mounted() {},
  methods: {}
};
</script>

<style lang="less">
#newshy {
  overflow: hidden;
  .content-child{
      padding: 15px 10px;
      img{
          width: 100%;
      }
  }
}
</style>


